
@import "../../../node_modules/highlight.js/styles/github.css";
@media screen and (max-width: 768px) {
.main > #book-list {
    z-index: 1002;
    left: -200px !important;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.main > #note-list {
    z-index: 1000;
    left: 0px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.main > #show-book-btn {
    z-index: 1001;
    position: fixed;
    width: 20px;
    height: 100%;
    top: 60px;
    bottom: 0;
    left: 0;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.main > #show-book-btn:active {
    background-color: rgba(0,0,0,0.1);
}
.main > #show-book-btn > .el-icon-arrow-right,
  .main > #show-book-btn > .el-icon-arrow-left {
    position: absolute;
    top: 50%;
    font-size: 20px;
}
.main > #show-book-btn > .el-icon-arrow-left {
    display: none;
}
.main > #show-book-check:checked ~ #book-list {
    left: 0 !important;
}
.main > #show-book-check:checked ~ #show-book-btn {
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
}
.main > #show-book-check:checked ~ #show-book-btn > .el-icon-arrow-right,
  .main > #show-book-check:checked ~ #show-book-btn > .el-icon-arrow-left {
    display: none;
}
}
.main {
  position: relative;
}
.main > #book-list {
  position: fixed;
  overflow-x: hidden;
  overflow-y: auto;
  width: 200px;
  top: 60px;
  left: 0;
  bottom: 0;
  border-right: 1px solid rgba(0,0,0,0.4);
  background-color: #eef1f6;
}
.main > .note-list {
  position: fixed;
  overflow: auto;
  top: 60px;
  left: 200px;
  right: 0;
  bottom: 0;
}
.main .md-preview {
  position: relative;
  min-height: 50px;
  padding: 5px;
  border: 1px solid #bfcbd9;
  border-radius: 4px;
}
.main .md-preview:before {
  content: '';
  position: absolute;
  top: 5px;
  right: 10px;
  color: #bfcbd9;
}
.main .md-preview > table {
  border: 1px solid #808080;
}


